import React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import TopSection from "../components/sections/TopSection"
import ProjectsSection from "../components/sections/ProjectsSection"
import { ThemeProvider, createTheme } from "@mui/material"
import ExperienceSection from "../components/sections/ExperienceSection"
import SkillsSection from "../components/sections/SkillsSection"
import AboutSection from "../components/sections/AboutSection"
import BottomSection from "../components/sections/BottomSection"
import Footer from "../components/sections/Footer"
import AppBar from '../components/sections/AppBar';
import smoothscroll from 'smoothscroll-polyfill';

const theme = createTheme({
    typography: {
        fontFamily: ['"Source Sans Pro"', '"sans-serif"'].join(','),
        
    },
    
    palette: { mode: 'dark'},
    components: {
        MuiButton: {
            styleOverrides: {
                root: {
                    letterSpacing: "0.05em",
                    fontWeight: '500',
                    fontSize: '15px'
                }
            }
        }
    }
    
});

export const RefsContext = React.createContext()

const IndexPage = () => {
    
    const allRefs = {
        projectsRef:   React.createRef(),
        experienceRef: React.createRef(),
        skillsRef :    React.createRef(),
        educationRef:  React.createRef(),
        aboutRef:      React.createRef(),
        resumeRef:     React.createRef(),
        emailRef:      React.createRef(),
    }

    React.useEffect(() => {
        smoothscroll.polyfill();
    }, [])

    return (
        <ThemeProvider theme={theme}>
            <Layout>
                <RefsContext.Provider value={allRefs}>
                    <Seo title="Home" />
                    <AppBar/>
                    <TopSection/>
                    <ProjectsSection/>
                    <ExperienceSection/>
                    <SkillsSection/>
                    <AboutSection/>
                    <BottomSection/>
                    <Footer/>
                </RefsContext.Provider>
            </Layout>
        </ThemeProvider>
    )
}   

export default IndexPage
