import React, { useContext, useState, useEffect } from 'react';
import { wrap, main, textArea, after, before, cardWrap } from './Education.module.scss';
import EducationTitle from './subcomponents/EducationTitle';
import brainImg from '../../../images/about/brain-img.png';
import SideLogo from './subcomponents/SideLogo';
import { RefsContext } from '../../../pages/index';
import sdsu from '../../../images/education/sdsu-logo.png';
import gross from '../../../images/education/logo-grossmont.png';
import SchoolCard from './subcomponents/SchoolCard';
import { isSafari, fullBrowserVersion} from 'react-device-detect';
import { isOldSafari } from '../../helpers';

const educations = [
    {
        imageSrc: sdsu,
        degree: "B.B.A., Management Information Systems"
    },
    {
        imageSrc: gross,
        degree: "A.S., Computer Programming"
    }
];

const Education = () => {
    const context   = useContext(RefsContext);
    const oldSafari = isOldSafari(isSafari, fullBrowserVersion);
    const [width, setWidth] = useState(0);

    useEffect(() => {
        if (typeof window === 'undefined') return;
      
        const handleResize = () => setWidth(window.innerWidth);
        window.addEventListener('resize', handleResize);
        return () => {
            window.removeEventListener('resize', handleResize)
        };
    });

    return (
        <div className={wrap}>
            <div className={before}></div>
            <div className={main} ref={context.educationRef}>
                <div className={textArea} style={oldSafari && width > 768 ? {marginRight: '3rem'} : {}}>
                    <EducationTitle/>
                    <div className={cardWrap}>
                    {
                        educations.map((e, i) => <SchoolCard key={i} {...e}/>)
                    }
                    </div>
                </div>
                <SideLogo imgSrc={brainImg}/>
            </div>
            <div className={after}>

            </div>
        </div>
    );
};

export default Education;