import { List, ListItem, ListItemIcon, ListItemText } from '@mui/material';
import React, {useContext} from 'react';
import ImportantDevicesIcon from '@mui/icons-material/ImportantDevices';
import BuildIcon from '@mui/icons-material/Build';
import AnimationIcon from '@mui/icons-material/Animation';
import SchoolIcon from '@mui/icons-material/School';
import PsychologyIcon from '@mui/icons-material/Psychology';
import FileDownloadIcon from '@mui/icons-material/FileDownload';
import EmailIcon from '@mui/icons-material/Email';
import { RefsContext } from '../../../pages/index';
import { icon, text } from './SideMenu.module.scss';
const items = [
    {
        title: "Projects",
        icon: <ImportantDevicesIcon className={icon}/>
    }, 
    {
        title: "Experience",
        icon: <BuildIcon className={icon}/>
    },
    {
        title: "Skills",
        icon: <AnimationIcon className={icon}/>
    },
    {
        title: "Education",
        icon: <SchoolIcon className={icon}/>
    },
    {
        title: "About Me",
        icon: <PsychologyIcon className={icon}/>
    },
    {
        title: "Resume",
        icon: <FileDownloadIcon className={icon}/>
    },
    {
        title: "Get In Touch",
        icon: <EmailIcon className={icon}/>
    }
]
const SideMenu = ({ open }) => {
    const allRefs = useContext(RefsContext);

    function handleClick(item) {
        let top;
        switch (item) {
          case "Projects":
            top = allRefs.projectsRef.current.getBoundingClientRect().top + window.scrollY;
            break;
          case "Experience":
            top = allRefs.experienceRef.current.getBoundingClientRect().top + window.scrollY
            break;
          case "Skills":
            top = allRefs.skillsRef.current.getBoundingClientRect().top + window.scrollY
            break;
          case "Education":
            top = allRefs.educationRef.current.getBoundingClientRect().top + window.scrollY
            break;
          case "About Me":
            top = allRefs.aboutRef.current.getBoundingClientRect().top + window.scrollY
            break;
          case "Resume":
            top = allRefs.resumeRef.current.getBoundingClientRect().top + window.scrollY - 50
            break;
          case "Get In Touch":
            top = allRefs.emailRef.current.getBoundingClientRect().top + window.scrollY - 200
            break;
          default:
            return
        }
        window.scroll({
            top,
            behavior: "smooth",
        })
    }
    return (
        <List>
            {
                items.map((item, idx) => (
                    <ListItem onClick={() => handleClick(item.title)} key={idx} button>
                        <ListItemIcon>
                            {item.icon}
                        </ListItemIcon>
                        <ListItemText className={text}>
                            {item.title}
                        </ListItemText>
                    </ListItem>
                ))
            }
        </List>
    );
};

export default SideMenu;