import React, { useContext } from 'react';
import {
    wrap,
    skillsWrap
} from './SkillsSection.module.scss';
import html from '../../images/skills/html-logo.png';
import css from '../../images/skills/css-logo.png';
import docker from '../../images/skills/docker-logo.png';
import js from '../../images/skills/js-logo.png';
import mongodb from '../../images/skills/mongodb-icon.png';
import nodejs from '../../images/skills/nodejs-logo.png';
import aws from '../../images/skills/aws-logo.png';
import react from '../../images/skills/react-logo.png';
import redux from '../../images/skills/redux-logo.png';
import responsive from '../../images/skills/responsive-design.png';
import sass from '../../images/skills/sass-logo.png';
import wordpress from '../../images/skills/wordpress-logo.png';
import typescript from '../../images/skills/typescript-logo.png';
import mui from '../../images/skills/mui-logo.png';
import semantic from '../../images/skills/semantic-ui-logo.png';
import sql from '../../images/skills/sql-logo.png';
import { RefsContext } from '../../pages/index';
import Skill from './SkillsSection/Skill';
import ExperienceTitle from './ExperienceSection/ExperienceTitle';

const skills = [
    { "React": react },
    { "Redux": redux },
    { "NodeJS": nodejs },
    { "MongoDB": mongodb },
    { "TypeScript": typescript},
    { "JavaScript": js },
    { "HTML5": html},
    { "CSS3": css },
    { "SASS": sass },
    { "Responsive Design": responsive },
    { "Material-UI": mui},
    { "Semantic-UI": semantic },
    { "Amazon Web Services": aws },
    { "Docker": docker },
    { "SQL": sql},
    { "Wordpress": wordpress }
];






const SkillsSection = () => {
    const context = useContext(RefsContext);

    return (

        <div ref={context.skillsRef} className={wrap}>
            <ExperienceTitle title="SKILLS" backgroundColor="#161616"/>
            <div className={skillsWrap}>
            {
                skills.map((skill, i) => {
                    const key = Object.keys(skill)[0];
                    return <Skill key={i} imageSrc={skill[key]} name={key}/>
                })
            }
            </div>
        </div>
    );
};

export default SkillsSection;